import { createRouter, createWebHashHistory } from 'vue-router';

const title = '艾里攝影工作室';

const routes = [
  {
    path: '/',
    name: 'index',
    component: () => import('@/views/Index.vue'),
    children: [
      {
        path: '/',
        name: 'home',
        component: () => import('@/views/front/Home.vue'),
        meta: {
          title: `${title} - 首頁`,
        },
      },
      {
        path: '/about',
        name: 'about',
        component: () => import('@/views/front/About.vue'),
        meta: {
          title: `${title} - 關於艾里`,
        },
      },
      {
        path: '/photographs',
        name: 'photographs',
        component: () => import('@/views/front/Photographs.vue'),
        meta: {
          title: `${title} - 攝影作品`,
        },
      },
      {
        path: '/projects',
        name: 'projects',
        component: () => import('@/views/front/Projects.vue'),
        meta: {
          title: `${title} - 攝影作品`,
        },
      },
      {
        path: '/project',
        name: 'project',
        component: () => import('@/views/front/Project.vue'),
        meta: {
          title: `${title} - 攝影方案細節`,
        },
      },
      {
        path: '/appointment',
        name: 'appointment',
        component: () => import('@/views/front/Appointment.vue'),
        meta: {
          title: `${title} - 線上預約`,
        },
      },
      {
        path: '/contact',
        name: 'contact',
        component: () => import('@/views/front/Contact.vue'),
        meta: {
          title: `${title} - 聯絡我們`,
        },
      },
      {
        path: '/login',
        name: 'login',
        component: () => import('@/views/Login.vue'),
        meta: {
          title: `${title} - 登入`,
        },
      },
    ],
  },
  {
    path: '/admin',
    name: 'admin',
    component: () => import('@/views/admin/Dashboard.vue'),
    children: [
      {
        path: '/admin',
        name: 'adminProject',
        component: () => import('@/views/admin/Projects.vue'),
        meta: {
          title: `${title} - 方案管理`,
        },
      },
      {
        path: '/appointment',
        name: 'adminAppointment',
        component: () => import('@/views/admin/Appointment.vue'),
        meta: {
          title: `${title} - 預約管理`,
        },
      },
    ],
  },
  {
    path: '/:pathMatch(.*)*',
    name: '404',
    component: () => import('@/views/The404.vue'),
    meta: {
      title,
    },
  },
];

const router = createRouter({
  history: createWebHashHistory(),
  routes,
  linkActiveClass: 'active',
});

router.beforeEach((to) => {
  if (to.meta.title) {
    document.title = to.meta.title;
  }
});

export default router;
